"use client";
import { useEffect, useMemo, useState } from "react";
import { AdminLayout } from "@/components/layout/admin-layout";
import StatCard from "@/components/dashboard/stat-card";
import SalesChart from "@/components/dashboard/sales-chart";
import { Card } from "@/components/ui/card";
import { Alert } from "@/components/ui/alert";
import { Button } from "@/components/ui/button";
import { useAuth } from "@/context/auth-context";
import { cn } from "@/app/utils/cn";
import { motion, AnimatePresence } from "framer-motion";
import { ProtectedLayout } from "@/components/layout/protected-layout";

// ==============================
// Interfaces
// ==============================
interface DashboardStats {
  totalSales: number;
  fuelInventory: number;
  activePumps: number;
  attendants: number;
}

// ==============================
// Floating Toggle Menu Component
// ==============================
const QuickMenu = ({ isDarkMode }: { isDarkMode: boolean }) => {
  const [isOpen, setIsOpen] = useState(false);

  const links = [
    { label: "Dashboard", href: "/admin/dashboard", icon: "üìä" },
    { label: "Sales Records", href: "/admin/sales-records", icon: "üí∞" },
    { label: "Reports", href: "/admin/reports", icon: "‚õΩ" },
    { label: "Pumps & Attendants", href: "/admin/pump-attendant", icon: "üë•" },
    { label: "Settings", href: "/admin/settings", icon: "‚öôÔ∏è" },
  ];

  return (
    <div className="fixed bottom-6 right-6 z-50 flex flex-col items-end gap-3">
      {/* Toggle Button */}
      <Button
        variant="secondary"
        onClick={() => setIsOpen(!isOpen)}
        className={cn(
          "w-14 h-14 rounded-full text-2xl flex items-center justify-center shadow-lg",
          isDarkMode
            ? "bg-slate-900 text-white hover:bg-slate-800 border border-white/10"
            : "bg-white text-slate-900 hover:bg-slate-200 border border-slate-300"
        )}
      >
        ‚ò∞
      </Button>

      {/* Animated Popup Menu */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0, y: 15 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: 15 }}
            transition={{ duration: 0.2 }}
            className={cn(
              "w-52 rounded-2xl shadow-xl overflow-hidden ring-1 ring-black/10",
              isDarkMode ? "bg-slate-900 text-white" : "bg-white text-slate-900"
            )}
          >
            {links.map((link) => (
              <a
                key={link.href}
                href={link.href}
                onClick={() => setIsOpen(false)}
                className={cn(
                  "flex items-center gap-3 px-4 py-3 text-sm font-medium transition-colors",
                  isDarkMode
                    ? "hover:bg-indigo-600 hover:text-white"
                    : "hover:bg-indigo-100 hover:text-indigo-700"
                )}
              >
                <span>{link.icon}</span>
                <span>{link.label}</span>
              </a>
            ))}
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

// ==============================
// Main Dashboard Page
// ==============================
export default function DashboardPage() {
  const { isAuthenticated, user, logout, register } = useAuth();
  const [stats, setStats] = useState<DashboardStats>({
    totalSales: 0,
    fuelInventory: 0,
    activePumps: 0,
    attendants: 0,
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [newUserName, setNewUserName] = useState("");
  const [newUserEmail, setNewUserEmail] = useState("");
  const [newUserPassword, setNewUserPassword] = useState("");
  const [newUserRole, setNewUserRole] = useState("attendant");
  const [registerError, setRegisterError] = useState("");
  const [registerLoading, setRegisterLoading] = useState(false);
  const [isDarkMode, setIsDarkMode] = useState(true);

  // Fetch mock dashboard data
  useEffect(() => {
    const fetchDashboardData = async () => {
      if (!isAuthenticated) {
        setLoading(false);
        setError("User not authenticated");
        return;
      }
      try {
        await new Promise((resolve) => setTimeout(resolve, 800)); // Mock delay
        setStats({
          totalSales: 1284,
          fuelInventory: 48750,
          activePumps: 12,
          attendants: 28,
        });
      } catch (err) {
        setError("Failed to fetch dashboard data");
      } finally {
        setLoading(false);
      }
    };
    fetchDashboardData();
  }, [isAuthenticated]);

  const quickActions = useMemo(
    () => [
      { label: "Restock Premium", accent: "from-sky-400 to-cyan-500" },
      { label: "Schedule Maintenance", accent: "from-amber-400 to-orange-500" },
      { label: "Assign Shift", accent: "from-emerald-400 to-teal-500" },
    ],
    []
  );

  // Register a new user
  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault();
    setRegisterError("");
    setRegisterLoading(true);
    try {
      await register(newUserName, newUserEmail, newUserPassword);
      setNewUserName("");
      setNewUserEmail("");
      setNewUserPassword("");
      setNewUserRole("attendant");
      alert("User registered successfully!");
    } catch (err: any) {
      setRegisterError(err.message || "Registration failed");
    } finally {
      setRegisterLoading(false);
    }
  };

  const toggleTheme = () => setIsDarkMode((prev) => !prev);

  return (
    <ProtectedLayout>
      <div className="flex flex-col gap-6">
          {/* Header */}
          <div className="flex flex-wrap items-center justify-between gap-4">
            <div>
              <motion.h1
                initial={{ opacity: 0, y: 12 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.35 }}
                className="text-3xl font-semibold tracking-tight"
              >
                Admin Command Center
              </motion.h1>
              <p className="mt-2 max-w-xl text-sm text-slate-600 dark:text-slate-400">
                Real-time insight into fuel operations, pump performance, and
                team coordination.
              </p>
            </div>

            <div className="flex items-center gap-3">
              <Button
                variant="secondary"
                onClick={toggleTheme}
                className={cn(
                  "shadow-lg",
                  isDarkMode
                    ? "border border-white/10 bg-slate-900/40 text-white hover:bg-slate-900/60"
                    : "border border-slate-200/60 bg-white/90 text-slate-800 hover:bg-white"
                )}
              >
                Switch to {isDarkMode ? "Light" : "Dark"} Mode
              </Button>
              <Button
                onClick={logout}
                variant="danger"
                className={cn(
                  "shadow-lg",
                  isDarkMode ? "shadow-red-900/40" : "shadow-red-400/30"
                )}
              >
                Logout
              </Button>
            </div>
          </div>

          {/* Alerts */}
          {error && <Alert variant="destructive">{error}</Alert>}
          {!isAuthenticated && (
            <Alert variant="warning">
              Your session is in demo mode. Connect to the production backend to
              enable live metrics.
            </Alert>
          )}

          {/* Stats Section */}
          <section
            className={cn(
              "grid grid-cols-1 gap-6 md:grid-cols-2 xl:grid-cols-4",
              isDarkMode ? "text-white" : "text-slate-900"
            )}
          >
            <StatCard
              title="Total Sales"
              value={loading ? "--" : stats.totalSales}
              icon={<span role="img" aria-label="Money">üí∞</span>}
              trendLabel="+8.4%"
              trendValue="vs last week"
              dark={isDarkMode}
            />
            <StatCard
              title="Fuel Inventory (L)"
              value={loading ? "--" : stats.fuelInventory.toLocaleString()}
              icon={<span role="img" aria-label="Fuel">‚õΩ</span>}
              trendLabel="Stable"
              trendValue="2% variance"
              dark={isDarkMode}
            />
            <StatCard
              title="Active Pumps"
              value={loading ? "--" : stats.activePumps}
              icon={<span role="img" aria-label="Pump">üîå</span>}
              trendLabel="3 offline"
              trendValue="due to maintenance"
              dark={isDarkMode}
            />
            <StatCard
              title="Attendants"
              value={loading ? "--" : stats.attendants}
              icon={<span role="img" aria-label="Team">üë•</span>}
              trendLabel="+4 new"
              trendValue="onboarded"
              dark={isDarkMode}
            />
          </section>

          {/* Charts and Quick Actions */}
          <section className="grid grid-cols-1 gap-6 lg:grid-cols-5">
            <Card
              className={cn(
                "lg:col-span-3",
                isDarkMode
                  ? "bg-slate-900/50 text-slate-100 ring-1 ring-white/10"
                  : "bg-white/95 text-slate-900 ring-1 ring-slate-900/10"
              )}
            >
              <div className="flex flex-wrap items-center justify-between gap-3">
                <div>
                  <h2 className="text-lg font-semibold">
                    Weekly Sales Overview
                  </h2>
                  <p className="text-sm text-slate-500 dark:text-slate-300">
                    Monitoring weekly throughput across all stations
                  </p>
                </div>
                <Button
                  variant="ghost"
                  className={cn(
                    "rounded-full px-4 py-2",
                    isDarkMode
                      ? "text-white hover:bg-white/10"
                      : "text-slate-600 hover:bg-slate-100"
                  )}
                >
                  Download Report
                </Button>
              </div>
              <div className="mt-6">
                <SalesChart dark={isDarkMode} />
              </div>
            </Card>

            <div className="space-y-6 lg:col-span-2">
              <Card
                className={cn(
                  isDarkMode
                    ? "bg-slate-900/50 text-slate-100 ring-1 ring-white/12"
                    : "bg-white/95 text-slate-900 ring-1 ring-slate-200/60"
                )}
              >
                <h2 className="text-lg font-semibold">Live Alerts</h2>
                <div className="mt-4 space-y-3 text-sm leading-relaxed">
                  <Alert variant="warning">
                    Super 95 inventory dipped below 20%
                  </Alert>
                  <Alert variant="default">
                    New compliance report ready for review
                  </Alert>
                  <Alert variant="success">
                    Pump 3 maintenance completed
                  </Alert>
                </div>
              </Card>

              <Card
                className={cn(
                  isDarkMode
                    ? "bg-slate-900/50 text-slate-100 ring-1 ring-white/12"
                    : "bg-white/95 text-slate-900 ring-1 ring-slate-200/60"
                )}
              >
                <h2 className="text-lg font-semibold">Quick Actions</h2>
                <div className="mt-4 grid gap-3 sm:grid-cols-2">
                  {quickActions.map((action) => (
                    <button
                      key={action.label}
                      className={cn(
                        "group flex items-center justify-between overflow-hidden rounded-2xl bg-gradient-to-br px-4 py-3 text-left text-sm font-medium text-white shadow-lg transition-transform hover:-translate-y-1 hover:shadow-2xl focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2",
                        action.accent
                      )}
                    >
                      <span>{action.label}</span>
                      <span
                        aria-hidden
                        className="text-lg transition-transform duration-300 group-hover:translate-x-1"
                      >
                        ‚Üí
                      </span>
                    </button>
                  ))}
                </div>
              </Card>
            </div>
          </section>

          {/* Register Form for Admin */}
          {user?.role === "ADMIN" && (
            <Card
              className={cn(
                isDarkMode
                  ? "bg-slate-900/50 text-slate-100 ring-1 ring-white/12"
                  : "bg-white/95 text-slate-900 ring-1 ring-slate-200/60"
              )}
            >
              <div className="flex flex-wrap items-center justify-between gap-3">
                <div>
                  <h2 className="text-lg font-semibold">
                    Register New Team Member
                  </h2>
                  <p className="text-sm text-slate-500 dark:text-slate-400">
                    Provision secure platform access for operations staff.
                  </p>
                </div>
                <div className="text-xs uppercase tracking-wide text-slate-500 dark:text-slate-300">
                  Role: {user.role}
                </div>
              </div>

              {registerError && (
                <div className="mt-4">
                  <Alert variant="destructive">{registerError}</Alert>
                </div>
              )}

              <form
                onSubmit={handleRegister}
                className="mt-6 grid gap-4 md:grid-cols-2"
              >
                <label className="flex flex-col gap-2">
                  <span className="text-xs font-medium uppercase tracking-wide text-slate-500 dark:text-slate-400">
                    Full Name
                  </span>
                  <input
                    type="text"
                    value={newUserName}
                    onChange={(e) => setNewUserName(e.target.value)}
                    required
                    className="rounded-xl border border-slate-200/60 bg-white/90 px-4 py-2 text-sm text-slate-900 shadow-inner transition-shadow focus:border-indigo-500 focus:outline-none focus:ring-2 focus:ring-indigo-400/40 dark:border-white/10 dark:bg-slate-900/50 dark:text-white dark:placeholder:text-white/40"
                    placeholder="Full name"
                    autoComplete="name"
                  />
                </label>
                <label className="flex flex-col gap-2">
                  <span className="text-xs font-medium uppercase tracking-wide text-slate-500 dark:text-slate-400">
                    Email
                  </span>
                  <input
                    type="email"
                    value={newUserEmail}
                    onChange={(e) => setNewUserEmail(e.target.value)}
                    required
                    className="rounded-xl border border-slate-200/60 bg-white/90 px-4 py-2 text-sm text-slate-900 shadow-inner transition-shadow focus:border-indigo-500 focus:outline-none focus:ring-2 focus:ring-indigo-400/40 dark:border-white/10 dark:bg-slate-900/50 dark:text-white dark:placeholder:text-white/40"
                    placeholder="email@fuelhq.io"
                    autoComplete="email"
                  />
                </label>
                <label className="flex flex-col gap-2">
                  <span className="text-xs font-medium uppercase tracking-wide text-slate-500 dark:text-slate-400">
                    Password
                  </span>
                  <input
                    type="password"
                    value={newUserPassword}
                    onChange={(e) => setNewUserPassword(e.target.value)}
                    required
                    className="rounded-xl border border-slate-200/60 bg-white/90 px-4 py-2 text-sm text-slate-900 shadow-inner transition-shadow focus:border-indigo-500 focus:outline-none focus:ring-2 focus:ring-indigo-400/40 dark:border-white/10 dark:bg-slate-900/50 dark:text-white dark:placeholder:text-white/40"
                    placeholder="Secure password"
                    autoComplete="new-password"
                  />
                </label>
                <label className="flex flex-col gap-2">
                  <span className="text-xs font-medium uppercase tracking-wide text-slate-500 dark:text-slate-400">
                    Role
                  </span>
                  <select
                    value={newUserRole}
                    onChange={(e) => setNewUserRole(e.target.value)}
                    className="rounded-xl border border-slate-200/60 bg-white/90 px-4 py-2 text-sm text-slate-900 shadow-inner transition-shadow focus:border-indigo-500 focus:outline-none focus:ring-2 focus:ring-indigo-400/40 dark:border-white/10 dark:bg-slate-900/50 dark:text-white"
                  >
                    <option value="attendant">Attendant</option>
                    <option value="supplier">Supplier</option>
                    <option value="admin">Admin</option>
                  </select>
                </label>

                <div className="flex justify-end md:col-span-2">
                  <Button
                    type="submit"
                    className={cn(
                      "w-full md:w-auto",
                      isDarkMode
                        ? "shadow-indigo-900/40"
                        : "shadow-indigo-200/60"
                    )}
                  >
                    {registerLoading ? "Registering..." : "Register User"}
                  </Button>
                </div>
              </form>
            </Card>
          )}
        </div>

        {/* Floating Toggle Menu */}
        <QuickMenu isDarkMode={isDarkMode} />
    </ProtectedLayout>
  );
}
